
import React, { useEffect, useState } from "react";
import "./ViewParticipants.css";

const ViewParticipants = () => {
  const [participants, setParticipants] = useState([]);

  useEffect(() => {
    fetch("http://localhost:5500/api/participants")
      .then(res => res.json())
      .then(data => setParticipants(data))
      .catch(err => console.error(err));
  }, []);

  return (
    <div className="participants-wrap">
      <h1 className="participants-title">Event Participants</h1>
      <p className="participants-subtitle">
        Meet all the amazing people taking part in this event!
      </p>

      <div className="participants-grid">
        {participants.map((p, idx) => (
          <div key={idx} className="participant-card">
            <img src={p.image} alt={p.name} className="participant-image" />
            <h3>{p.name}</h3>
            <p>{p.bio}</p>
          </div>
        ))}
      </div>
    </div>
  );
};

export default ViewParticipants;
